import React from 'react';
import { MDXRenderer } from 'gatsby-plugin-mdx';
import { graphql } from 'gatsby';

import Footer from './footer';
import Navbar from './navbar';

interface Props {
  pageContext: {
    locale: string;
  };
  data: {
    mdx: {
      id: string;
      body: string;
      frontmatter: {
        hero: object;
        concept: object[];
        showcase: object[];
      };
    };
  };
}

const Layout = ({ data, pageContext }: Props) => {
  const { locale } = pageContext;
  return (
    <div className="relative overflow-hidden">
      <Navbar />
      <MDXRenderer frontmatter={data.mdx.frontmatter}>{data.mdx.body}</MDXRenderer>
      <Footer locale={locale} />
    </div>
  );
};

export default Layout;

export const pageQuery = graphql`
  query MDXQuery($id: String!) {
    mdx(id: { eq: $id }) {
      id
      body
      frontmatter {
        hero {
          title
          description
        }
        video {
          title
          description
          url
        }
        showcase {
          title
          products {
            title
            description
            image {
              publicURL
            }
          }
        }
        concept {
          title
          steps {
            description
            image {
              publicURL
            }
          }
        }
        whitepaper {
          title
          description
          file {
            publicURL
          }
          pdfButton {
            label
          }
        }
        newsletter {
          title
          description
          form {
            firstNameLabel
            emailLabel
            buttonLabel
          }
        }
      }
    }
  }
`;
